


















































import Component from 'vue-class-component';
import Vue from 'vue';
import { ElForm } from 'element-ui/types/form';
import TagSelect from '@/components/form/TagSelect.vue';
import { Async } from '@/assets/utils/util';
import { Ref } from 'vue-property-decorator';

interface SelOption {
    value?: any;
    label?: string;
    children?: SelOption[];
}

@Component({
    components: {
        'tag-select': TagSelect
    }
})
export default class Submissio extends Vue {
    public form: any = {
        base: [],
        port: 60000,
        protocol: ''
        // tagNo: ''
    };

    public base: SelOption[] = [];
    public protocols: IProtocol[] = [];
    public effect: string = '';

    @Ref('form') private readonly elForm!: ElForm;

    public created() {
        this.getBase();
        this.getProtocol();
    }

    @Async()
    public async onSubmit() {
        const { err, value } = await this.$async(this.elForm.validate());
        if (err) {
            return;
        }

        const res = await this.$http.post('/api/protocol/sendProtocol', {
            ...this.form,
            ip: this.form.base[1]
        });

        this.$message.success(res.message);
    }
    public selProtocol(content: string) {
        const protocol = this.protocols.find(v => v.content === content);
        this.effect = protocol ? protocol.effect : '';
    }

    @Async()
    private async getBase() {
        const res = await this.$http.get('/api/base/getall', {
            pageSize: 10000000,
            currentPage: 1
        });

        this.base.length = 0;
        const group: { [key: string]: SelOption[] } = {};
        res.pagedData.datas.forEach((v: IBaseStation) => {
            (group[v.groupCode] || (group[v.groupCode] = [])).push({
                value: v.ip,
                label: `${v.name}: ${v.id}; ${v.main ? '主' : '从'}基站`
            });
        });

        for (const [k, v] of Object.entries(group)) {
            this.base.push({
                label: `分组${k}`,
                children: v
            });
        }
    }

    @Async()
    private async getProtocol() {
        this.protocols = (await this.$http.get('/api/protocol/getall', {
            pageSize: 10000000,
            currentPage: 1
        })).pagedData.datas;
    }
}
